* {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: bold;
}

ol, ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  position: relative;
  overflow: hidden;
}

.container nav {
  position: absolute;
  top: 0px;
  left: 120px;
  z-index: 1;
  animation: fadein 1s forwards;
  animation-delay: 1s !important;
  opacity: 0;
}

@keyframes fadein {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.container nav ul li {
  float: left;
  width: 100px;
}

.container nav ul li a {
  display: block;
  color: #fff;
  font-size: 40px;
  text-decoration: underline;
}

.main__banner .main__bg {
  width: 72%;
  overflow: hidden;
  float: right;
}

.main__banner .main__bg1 {
  height: 900px;
  background: url(../images/bg9.jpg) 50% 0 no-repeat;
  background-size: cover;
  animation: Top 20s forwards ease-out;
  animation-delay: !important;
}

@keyframes Top {
  0% {
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
    -webkit-transform-origin: top;
    transform-origin: top;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: top;
    transform-origin: top;
  }
}

.main__banner .main__bg2 {
  position: relative;
  width: 28%;
  height: 900px;
  float: left;
  background-color: #5d3119;
}

.main__banner .main__bg2 .bg__bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  height: 450px;
  background-color: #be7f5c;
  animation: in_top 1s forwards;
  animation-delay: 0.1s !important;
  transform: translate3d(0px, -450px, 0);
}

@keyframes in_top {
  0% {
    -webkit-transform: translate3d(0px, -450px, 0);
    transform: translate3d(0px, -450px, 0);
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

